@import "normalize";
@import "_slick-theme.scss";
@import "slick";


$monserat:'Montserrat', sans-serif;
$crimson:'Crimson Text', serif;
$amsdam:"Amsdam", serif;
$black:#3e3d3d;

@mixin button {
   a:hover{
        background: $black;
        color: #fff;
        transition: 0.2s ease-in-out;
    }
}

@mixin title {
text-align: center;
    h2{
        text-transform: uppercase;   
    }
    p{
        padding-top: 40px;
        max-width: 998px;
        margin: 0 auto;
    }
}

@mixin social{
    &__social{
        display: flex;
        padding: 0;
        li{
            list-style-type: none;
          position: relative;
          margin-left: 10px;
            a{
                text-decoration: none;
                color: #aaaaaa;
                display: block;
                padding: 0px 5px;
            }
          
        }
        li::after{
            position: absolute;
            left: 26px;
            top: 3px;
            display: block;
            content: "";
            width: 1px;
            height: 10px;
            background: #aaaaaa;
        }
    }
}

h1,h2,h3,h4{
    padding: 0;
    margin: 0;
}

h1{
    font-family: $amsdam;
    font-size: 37px;
    font-weight: 400;
    letter-spacing: 2.4px;
    color: #fff;
}

h2{
    font-family: $amsdam;
    font-size: 20px;
}

h3{
    font-family: $monserat;
    font-size: 13px;
    font-weight: 400;
}
h1,h2,h3{
    text-transform: uppercase;
}

p{
    font-family: $crimson;
    font-weight: 400;
    font-size: 14px;
    letter-spacing: 1.1px;
}

body{
    padding: 0;
    margin: 0;
}

.container{
    margin: 0 auto;
    padding-left: 10px;
    padding-right: 10px;
    max-width: 1170px;
}

.header{
    padding-top: 4px;
    padding-bottom: 9px;
    &-page{
        display: flex;
        justify-content: space-between;
        align-items: center;
        position: relative;
        @media (max-width:1024px) {
          justify-content: space-evenly;
        }
        &__logo{
            text-decoration: none;
            padding-left: 6px;
            span{
                font-family: $monserat;
                    font-weight: 400;
                    color: #757575;
                    font-size: 18px;
                    padding-left: 10px;
                    letter-spacing: 1.7px;
            }
        }
            &__button{
                display: none;
                background: transparent;
                height: 21px;
                width: 30px;
                position: relative;
                
                @media (max-width:1024px) {
                    display: block;
                    
                }
                span,span::before,span::after{
                    width: 30px;
                    height: 2px;
                    content: "";
                    background: $black;
                    position: absolute;
                    top:50%;
                    display: block;
                }
                span::before{
                    transform: translatey(6px);
                    transition: 0.2s ease-in-out;
                }
                span::after{

                transform: translatey(-8px);
                transition: 0.2s ease-in-out;
                }
            }
        &__menu{
            display: flex;
            @media (max-width:1024px) {
              transform: scale(0);
               flex-direction: column;
               position: absolute;
             padding: 0;
            }
            li{
                list-style-type: none;
                margin-left: 16px;
                @media (max-width:1024px) {
                 margin-left: 0;
                 padding-top: 5px;
                   
                  }
                a{
                    text-decoration: none;
                    font-family: $monserat;
                    font-weight: 400;
                    text-transform: uppercase;
                    color: #aaaaaa;
                    font-size: 12px;
                }
            }
        }
        &__social{
            display: flex;
            padding: 0;
         
            li{
                list-style-type: none;
              position: relative;
              margin-left: 10px;
                a{
                    text-decoration: none;
                    color: #aaaaaa;
                    display: block;
                    padding: 0px 5px;
                }
              
            }
            li::after{
                position: absolute;
                left: 26px;
                top: 3px;
                display: block;
                content: "";
                width: 1px;
                height: 10px;
                background: #aaaaaa;
            }
        }

    }
}

