.page-history{
    padding-top:56px;
    padding-bottom: 80px;
    &__content{
        @include title();
        padding-bottom: 126px;
        @media (max-width:800px) {
            padding-bottom: 20px;
        }
    }
    &__block{
        display: grid;
    
    grid-template-areas:". g ." "a g b" "c g d" "e g f" ". g .";
    grid-gap: 24px;
    @media (max-width:800px) {
       grid-column-gap: 10px;
       grid-template-areas: "a""b""c""d""e""f";
       justify-content: center;
    }
    &__item{
        background: transparent;
        border:2px solid $black;
        padding: 25px 20px;
        h3{
            span{
                display: block;
            }
        }
    

    &__one{
        grid-area: a;
    }
    &__two{
        grid-area: b;
       
    }
    &__three{
        grid-area: c;
    }
    &__four{
        grid-area: d;
    }
    &__five{
        grid-area: e;
    }
    &__six{
        grid-area: f;
    }
    &__line{
        content: "";
        display: block;
        grid-area: g;
        height: 100%;
        width: 2px;
        background: $black;
        position: relative;
        @media (max-width: 800px){
            display: none;
        }
        &__start{
            @media (max-width:800px) {
                display: none;
             }
            position: absolute;
            background: transparent;
            border: 2px solid $black;
            top: -60px;
            left: -29px;
            height: 56px;
            width: 56px;
            border-radius: 100%;
            text-align: center;
            p{
                color: $black;
                font-weight: 400;
                margin-top: 18px;
            }
        }
        &__end{
            @media (max-width:800px) {
               display: none;
             }
            position: absolute;
            background: $black;
            bottom: -56px;
            left: -26px;
            height: 56px;
            width: 56px;
            border-radius: 100%;
            text-align: center;
            &__item{
                color: #fff;
                margin-top: 33%;
            }
        }
    }
   /* &__line::before{
      
       }*/
    
}
}
