.page-footer-bg{
    background: url("../img/footer.jpg") no-repeat center;
    background-size: cover;
}
.page-footer{
    padding-top: 50px;
    padding-bottom: 36px;
    &__copy{
        font-weight: 600;
        font-family: $crimson;
        font-style: italic;
        padding-top: 97px;
        position: relative;
        color: #949494;
    }
    &__copy::before{
        content: "";
        position: absolute;
        top: 56px;
        background: #3e3e3e;
        height: 1px;
        width: 100%;
        display: block;
    }
    &__block{
        display: flex;
        justify-content: space-between;
       @media (max-width:1208px) {
           flex-direction: column;
           justify-content: center;
           text-align: center;
           
       }
        &__item{
            color: #fff;
            p{
                color: #949494;
                @media (max-width:1208px) {
                    margin: 0 auto;
                }
            }
            h3{
                padding-top: 38px;
                padding-bottom: 22px;
            }
            &__logo{
                text-decoration: none;
                padding-left: 6px;
                span{
                    font-family: $monserat;
                        font-weight: 400;
                        color: #757575;
                        font-size: 18px;
                        padding-left: 10px;
                        letter-spacing: 1.7px;
                }
            }
            &__contact{
                max-width: 300px;
            }
            &__addres{
               max-width: 227px;
            }
            &__text{
                max-width: 370px;
            }


            @include social();
            &__social{
                padding-top: 33px;
                @media (max-width:1208px) {
                   justify-content: center;
                   padding-top: 10px;
                   padding-bottom: 20px;
                }
            }
            
            input{
                box-sizing: border-box;
                outline: none;
                background: transparent;
                border: 2px solid #464646;
                padding: 20px 23px;
                width: 373px;
                margin-bottom: 20px;
                @media (max-width:440px) {
                   width: 100%;
                }
            }
            input:nth-child(2){
                margin-left: 33px;
                @media (max-width:880px) {
                    margin-left: 0;
                }
            }
            input:nth-child(4){
                margin-left: 33px;
                @media (max-width:880px) {
                    margin-left: 0;
                }
            }
            textarea{
               display: block;
                width: 100%;
                height: 94px;
                box-sizing: border-box;
                background: transparent;
                outline: none;
                resize: none;
                border: 2px solid #464646;
                padding: 20px 22px;
                margin-bottom: 20px;
                
            }
            button{
                background: #464646;
                border:2px solid #464646;
                color: #fff;
                font-size: 13px;
                width: 135px;
                height: 40px;
                font-family: $monserat;
                font-weight: 400;
                cursor: pointer;
                outline: none;
                transition: 0.3s ease-in-out;
            }
            button:hover{
                background: #fff;
                color: $black;
                transition: 0.3s ease-in-out;
                border: 0px;
            }
            &__tex{
                flex-basis: 30%;
            }
            &__form{
                flex-basis: 67%;
            }
        }
    }
}

.slick-dots li button:before {
 font-family: 'slick';
  font-size: 6px;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  content: '•'; /* <-- Вот этот символ */
  text-align: center;
  opacity: .25;
  color: #c5c5c5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


/* ANMATION */

.show{
  transition: 0.3s ease-in-out;
transform: scale(1);
}
.header-page__menu__active{
    background: #fff;
    transform: scale(1) translateY(121px) translateX(34px);
    transition: 0.2s ease-in-out;
  

}

.header-page__button__actvie{

    span{
        height: 0;
    }
    span::after{
        transform: rotate(45deg);
        transition: 0.2s ease-in-out;
    }
    span::before{
        transform: rotate(-45deg);
        transition: 0.2s ease-in-out;

    }
}

.slick-prev{
    @media (max-width:1240px) {
        left: 0;
    }
}
.slick-next{
    @media (max-width:1240px) {
        right:0;
    }
}

